<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>提现记录</p>
            </h3>
            <div class="searchInput">
                <div class="search_row">
                    <input class="my-search" v-model="value1" placeholder="输入提现单号查询" />
                    <i @click="submitHandler" class="icon-search"></i>
                    <div class="menuBox" align="right">
                        <i class="icon-menu" @click="openclick()"></i>
                    </div>
                </div>
            </div>
        </div>
        <div class="info_list">
            <pull-to @infinite-scroll="getMoreList" class="file-lists">
                <ul class="pull_ul">
                    <li v-for="item in dataList">
                        <p class="first" style="letter-spacing:0">单号：{{item.name}}</p>
                        <div class="second">状态：{{item.num}}</div>
                        <p class="third" style="color:#000;">时间：{{item.time}}</p>
                        <div class="box_right">
                            {{item.money}}元
                        </div>
                    </li>
                </ul>
            </pull-to>
        </div>
        <div class="timeSearch" :class="{on:openbox}">
            <div class="timeSearch_inner">
                <p>开始时间：</p>
                <div class="startTime">
                    <yd-cell-item>
                        <yd-datetime type="date" v-model="startTime" slot="right"></yd-datetime>
                    </yd-cell-item>
                </div>
                <p>结束时间：</p>
                <div class="endTime">
                    <yd-cell-item>
                        <yd-datetime type="date" v-model="endTime" slot="right"></yd-datetime>
                    </yd-cell-item>
                </div>
            </div>
            <div class="searchBtn clearfixed">
                <button @click="submitHandler">搜  索</button>
            </div>
        </div>
    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                page:1,
                page_number:10,
                startTime: "2009-01-01",
                endTime: "2009-01-01",
                openbox: false,
                value1: "",
                dataList: []
            }
        },
        methods: {
            openclick() {
                this.openbox = !this.openbox;
            },
            goLast() {
                this.$router.go(-1);
            },
            submitHandler() {
                this.getInfo();
                this.openbox = false;
            },
            async getMoreList() {
                console.log("翻页");
                this.page = this.page + 1;
                let res = await ajax.Get(dataURL.main.withdrawHistory, {
                    page: this.page,
                    page_number: this.page_number,
                    keywords: this.value1,
                    start_date:this.startTime!="2009-01-01"?this.startTime + " 00:00:00":"",
                    end_date:this.endTime!="2009-01-01"?this.endTime + " 23:59:59":"",
                })
                this.count = res.count;
                if (this.dataList.length >=res.count) {
                    this.page = this.page - 1;
                    this.$dialog.toast({
                        mes: "无更多数据",
                        timeout: 1500,
                        icon: "error"
                    });
                    return;
                }
                this.dataList = [...this.dataList, ...res.list];
            },
            getNextList(item) {
                this.$router.push({
                    path: "todayIncomeDetail",
                    query: { id: item.id }
                })
            },
            async getInfo() {
                let res = await ajax.Get(dataURL.main.withdrawHistory, {
                    page: this.page,
                    page_number: this.page_number,
                    keywords:this.value1,
                    start_date:this.startTime!="2009-01-01"?this.startTime + " 00:00:00":"",
                    end_date:this.endTime!="2009-01-01"?this.endTime + " 23:59:59":"",
                })
                this.dataList = res.list;
                console.log(res);
            },
        },
        created() {
            this.getInfo();
        }
    }
</script>


